/* ÈòüÂàóÁä∂ÊÄÅÁªÑ‰ª∂Ê†∑Âºè */

.queue-status-card {
  border-radius: 8px !important;
  border: 1px solid #e8e8e8 !important;
  background: rgba(255, 255, 255, 0.95) !important;
  backdrop-filter: blur(10px) !important;
}

.queue-status-card .ant-card-head {
  padding: 8px 16px !important;
  min-height: auto !important;
  border-bottom: 1px solid #f0f0f0 !important;
}

.queue-status-card .ant-card-head-title {
  font-size: 14px !important;
  font-weight: 600 !important;
}

.queue-status-card .ant-card-body {
  padding: 12px 16px !important;
}

/* ÈòüÂàóÊ¶ÇËßà */
.queue-overview {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 0;
}

.queue-overview .ant-badge {
  margin: 0 8px;
}

.queue-overview .anticon {
  color: #666;
  transition: color 0.3s ease;
}

.queue-overview .anticon:hover {
  color: #1890ff;
}

/* ËµÑÊ∫ê‰ΩøÁî®Áéá */
.resource-usage {
  width: 100%;
}

.resource-usage .ant-progress {
  margin-bottom: 4px !important;
}

.resource-usage .ant-progress-text {
  font-size: 11px !important;
  font-weight: 500 !important;
}

/* ÁªüËÆ°‰ø°ÊÅØ */
.queue-stats {
  text-align: center;
  line-height: 1.3;
}

/* Ê¥ªË∑ÉÊµÅ */
.active-streams {
  max-height: 120px;
  overflow-y: auto;
  padding: 4px 0;
}

.active-streams::-webkit-scrollbar {
  width: 4px;
}

.active-streams::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 2px;
}

.active-streams::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 2px;
}

.active-streams::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* ÊÄßËÉΩÊèêÁ§∫ */
.performance-tip {
  text-align: center;
  padding: 4px 8px;
  background: #fff7e6;
  border: 1px solid #ffd591;
  border-radius: 4px;
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
  .queue-status-card {
    position: relative !important;
    top: auto !important;
    right: auto !important;
    width: 100% !important;
    margin-bottom: 16px;
  }
}

/* Âä®ÁîªÊïàÊûú */
.queue-status-card {
  animation: slideInRight 0.3s ease-out;
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Âä†ËΩΩÁä∂ÊÄÅ */
.queue-status-card .ant-spin-container {
  min-height: 60px;
}

/* Badge Ê†∑Âºè‰ºòÂåñ */
.queue-overview .ant-badge-count {
  font-size: 10px !important;
  min-width: 16px !important;
  height: 16px !important;
  line-height: 16px !important;
  padding: 0 4px !important;
}

/* Progress Êù°Ê†∑Âºè‰ºòÂåñ */
.resource-usage .ant-progress-bg {
  transition: all 0.3s ease !important;
}

/* ÊñáÂ≠óÊ†∑Âºè */
.queue-stats .ant-typography {
  margin-bottom: 2px !important;
}

.active-streams .ant-typography {
  margin-bottom: 0 !important;
}

/* ÂàÜÂâ≤Á∫øÊ†∑Âºè */
.queue-status-card .ant-divider {
  margin: 8px 0 !important;
  border-color: #f0f0f0 !important;
}

/* ÊåâÈíÆÊ†∑Âºè */
.queue-status-card .ant-btn-text {
  padding: 0 4px !important;
  height: 20px !important;
  line-height: 20px !important;
}

.queue-status-card .ant-btn-text .anticon {
  font-size: 12px !important;
}

/* Â∑•ÂÖ∑ÊèêÁ§∫Ê†∑Âºè */
.ant-tooltip-inner {
  font-size: 11px !important;
}

/* ÊöóËâ≤‰∏ªÈ¢òÊîØÊåÅ */
@media (prefers-color-scheme: dark) {
  .queue-status-card {
    background: rgba(20, 20, 20, 0.95) !important;
    border-color: #434343 !important;
  }
  
  .queue-status-card .ant-card-head {
    border-bottom-color: #434343 !important;
  }
  
  .queue-status-card .ant-divider {
    border-color: #434343 !important;
  }
  
  .performance-tip {
    background: #2a1f0a !important;
    border-color: #d48806 !important;
  }
}
